import gsap from "gsap";
import TextPlugin from "gsap/TextPlugin";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(TextPlugin, ScrollTrigger);
gsap.config({autoKillThreshold: 0});

// Manually update animations using requestAnimationFrame
function updateAnimations() {
ScrollTrigger.update();
requestAnimationFrame(updateAnimations);
}

export function animateLogo() {
  const topSquare = document.querySelector(".top-square");
  const bottomSquare = document.querySelector(".bottom-square");
  const logoContainer = document.querySelector(".logo_container");
  const choixText = document.querySelector("#choix_text");
  const parfumElements = gsap.utils.toArray(".parfum");

  gsap.fromTo(
    topSquare,
    { y: -100 },
    {
      y: 0,
      duration: 1,
      ease: "back.out(1.4)",
    }
  );

  gsap.fromTo(
    bottomSquare,
    { y: 100 },
    {
      y: 0,
      duration: 1,
      ease: "back.out(1.4)",
    }
  );

  gsap.fromTo(
    logoContainer,
    {
      opacity: 0,
      scale: 0.8,
      filter: "blur(4px)",
    },
    {
      opacity: 1,
      scale: 1,
      filter: "none",
      duration: 2.5,
      ease: "sine.out",
    }
  );

  gsap.to(choixText, {
    duration: 1,
    ease: "steps(25)",
    text: "Choisissez votre parfum :",
  });

  gsap.fromTo(
    parfumElements,
    { y: 10, opacity: 0 },
    {
      y: 5,
      opacity: 1,
      duration: 1,
      ease: "expo.out",
      stagger: {
        each: 0.5,
      },
    }
  );

  gsap.fromTo(
    parfumElements,
    { y: 5 },
    {
      y: -5,
      duration: 1,
      ease: "expo.out",
      stagger: {
        each: 0.5,
      },
      repeat: -1,
    }
  );
}


export const text_mask = () => {
  gsap.to(".line_mask", {
    width: 0,
    scrollTrigger: {
      trigger: ".line_mask",
      scrub: 2,
    },
    stagger: 2,
    duration: 2,
    ease: "slow(0.1, 0.4, false)",
  });

  updateAnimations()
};


export const images_anim = (y, trigger, start, end) => {


  const backgroundImages = gsap.utils.toArray(".background_image");
  const elements = gsap.utils.toArray(".one, .two, .three");

  gsap.from(backgroundImages, {
    opacity: 0,
    y: -15,
    duration: 1,
    scrollTrigger: {
      trigger,
      toggleActions: "restart none none pause",
    },
  });

  const timeline = gsap.timeline({
    scrollTrigger: {
      trigger,
      start,
      end,
      pin: true,
      scrub: true,
      
      markers:true
    },
  });

  timeline.from(elements, {
    y,
    opacity: 0,
    duration: 1,
    ease: "expo.out",
    stagger: 1,
  });

  updateAnimations();
};

const textDiv =  gsap.utils.toArray(".text > div");
export const text_scroll = () => {
  const containerWidth = document.querySelector(".text_anim").offsetWidth;
  gsap.to( textDiv , {
    x: "-100%",
    duration: (3 * containerWidth) / 100,
    ease: "none",
    repeat: -1,
  });
};

export const animScrollImage = () => {
  gsap.to(".gateaux:not(.opposite) .image_gateaux", {
    width: "50%",
    duration: 1.2,
    scrollTrigger: {
      trigger: ".gateaux",
      pin: true,
      pinSpacing: false,
      scrub: 1,
    },
  });
  gsap.to(".gateaux.opposite .image_gateaux", {
    width: "50%",
    duration: 1.2,
    scrollTrigger: {
      trigger: ".gateaux.opposite",
      pin: true,
      pinSpacing: false,
      scrub: 1,
    },
  });
  updateAnimations()
};
